#editor-container{
  width: 80%;
  margin: 20px;
  display: block;
  margin: 10px auto;
  max-width: 600px;
  
}

#editor-header{
  border: 1px solid black;
  padding: 5px;
  font-family: 'russo One';
}

textarea{
  border: 1px solid black;
  width: 100%;
  height: 100%;
  resize: vertical;
  background-color: #c0d8d8;
  height: 200px;
  padding: 10px;
  transition: height 0.5s ease;
}

#minmax{
  cursor: pointer;
}

.full{
  height: 100vh;
}